@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/roboto-regular.woff2") format("woff2"), url("../fonts/roboto-regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/roboto-v30-cyrillic_latin-500.woff2") format("woff2"), url("../fonts/roboto-v30-cyrillic_latin-500.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Roboto";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/roboto-v30-cyrillic_latin-700.woff2") format("woff2"), url("../fonts/roboto-v30-cyrillic_latin-700.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/open-sans-v36-cyrillic_latin-regular.woff2") format("woff2"), url("../fonts/open-sans-v36-cyrillic_latin-regular.woff") format("woff");
}
@font-face {
  font-display: swap;
  font-family: "Open Sans";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/open-sans-v36-cyrillic_latin-700.woff2") format("woff2"), url("../fonts/open-sans-v36-cyrillic_latin-700.woff") format("woff");
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Roboto", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}
.wrapper--centred {
  justify-content: center;
  background: linear-gradient(180deg, rgba(222, 237, 255, 0.6) 0%, rgba(166, 204, 250, 0.5) 100%), url("../img/svg/background-overlay.svg") no-repeat 53.5% 50%/cover;
  background-attachment: fixed;
}
@media (max-width: 1279px) {
  .wrapper--centred {
    background: linear-gradient(180deg, rgba(222, 237, 255, 0.6) 0%, rgba(166, 204, 250, 0.5) 100%), url("../img/svg/background-overlay-tablet.svg") no-repeat center/cover;
    background-attachment: fixed;
  }
}
@media (max-width: 767px) {
  .wrapper--centred {
    background: linear-gradient(180deg, rgba(222, 237, 255, 0.6) 0%, rgba(166, 204, 250, 0.5) 100%), url("../img/svg/background-overlay-mobile.svg") no-repeat center/cover;
    background-attachment: fixed;
  }
}
.wrapper--centred main {
  flex-grow: 0;
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.container {
  width: 100%;
  max-width: 1180px;
  margin: 0 auto;
}
@media (max-width: 1279px) {
  .container {
    max-width: 700px;
  }
}
@media (max-width: 767px) {
  .container {
    max-width: 288px;
  }
}

.btn {
  font-family: "Open Sans", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 700;
  line-height: 1.5;
  color: #ffffff;
  display: inline-flex;
  padding: 12px 24px;
  background-color: #f91155;
  border: none;
  border-radius: 5px;
  cursor: pointer;
  transition: background-color 0.3s ease, color 0.3s ease, outline 0.3s ease, opacity 0.3s ease;
}
.btn--disabled {
  opacity: 0.6;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    background-color: #d8003f;
  }
}
.btn:active {
  background-color: #d8003f;
  color: #f9637c;
}
.btn:focus-visible {
  outline: 1px solid #ffffff;
  background-color: #d8003f;
  outline-offset: -1px;
}

.main-header {
  padding-top: 34px;
  padding-bottom: 26px;
}
@media (max-width: 1279px) {
  .main-header {
    padding-top: 15px;
  }
}
@media (max-width: 767px) {
  .main-header {
    padding-top: 22px;
    padding-bottom: 22px;
  }
}

.main-header__wrapper h1 {
  font-weight: 500;
  font-size: 36px;
  line-height: 1.52;
  color: #000000;
  text-transform: uppercase;
  text-align: center;
  margin: 0;
}
@media (max-width: 767px) {
  .main-header__wrapper h1 {
    font-size: 24px;
  }
}

.rooms {
  padding-bottom: 26px;
}
.rooms::before {
  content: "";
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  height: 100vh;
}

.rooms__list {
  margin: 0;
  padding: 0;
  list-style-type: none;
  display: flex;
  flex-wrap: wrap;
  column-gap: 32px;
  row-gap: 37px;
  justify-content: start;
}
@media (max-width: 1279px) {
  .rooms__list {
    justify-content: space-between;
    column-gap: 28px;
  }
}
@media (max-width: 767px) {
  .rooms__list {
    row-gap: 32px;
  }
}

@media (max-width: 1279px) {
  .rooms__item {
    flex-grow: 1;
  }
}

.room {
  display: flex;
  justify-content: center;
  position: relative;
}
@media (hover: hover) {
  .room:not(.is-reserved):hover:not(.focus-visible) .room__motivation {
    display: flex;
    opacity: 1;
    visibility: visible;
    pointer-events: all;
  }
}
.room.is-reserved .room__wrapper {
  pointer-events: none;
}
.room.is-reserved .room__wrapper::before {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.room__wrapper {
  width: 372px;
  height: 372px;
  padding: 24px;
  padding-bottom: 22px;
  border-radius: 12px;
  overflow: hidden;
  position: relative;
  transform: translateZ(0);
}
@media (max-width: 1279px) {
  .room__wrapper {
    width: 336px;
    height: 420px;
    padding-bottom: 24px;
  }
}
@media (max-width: 767px) {
  .room__wrapper {
    width: 288px;
    height: 347px;
    padding: 21px;
  }
}
.room__wrapper::before {
  position: absolute;
  content: "";
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(255, 255, 255, 0.4);
  border-radius: 12px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}

.room__content-wrapper {
  height: 100%;
  display: grid;
  grid-template-rows: 1fr 74px;
  position: relative;
}
@media (max-width: 1279px) {
  .room__content-wrapper {
    grid-template-rows: 1fr 136px;
  }
}
@media (max-width: 767px) {
  .room__content-wrapper {
    grid-template-rows: 1fr 128px;
  }
}

.room__description-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: end;
  padding-bottom: 20px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.2);
  overflow-y: auto;
}

.room__title-link {
  align-self: start;
  margin-bottom: 10px;
  position: relative;
}
.room__title-link::after {
  content: "";
  position: absolute;
  bottom: 4px;
  left: 0;
  right: 0;
  height: 1px;
  background-color: rgba(255, 255, 255, 0);
  z-index: 1;
  transition: background-color 0.3s ease;
}
@media (hover: hover) {
  .room__title-link:hover::after {
    background-color: #ffffff;
  }
}
.room__title-link:active .room__title {
  color: rgba(255, 255, 255, 0.5);
}
.room__title-link:active::after {
  background-color: rgba(255, 255, 255, 0);
}
.room__title-link:focus-visible {
  outline: none;
}
.room__title-link:focus-visible::after {
  background-color: #ffffff;
}

.room__title {
  font-weight: 700;
  font-size: 24px;
  line-height: 1.33;
  color: #ffffff;
  margin: 0;
  transition: color 0.3s ease;
  position: relative;
  z-index: 2;
}
@media (max-width: 767px) {
  .room__title {
    font-size: 20px;
    line-height: 1.3;
  }
}

.room__description {
  font-weight: 400;
  font-size: 16px;
  line-height: 1.5;
  color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif;
  margin: 0;
  overflow-y: auto;
}
@media (max-width: 767px) {
  .room__description {
    font-size: 14px;
  }
}

.room__price-container {
  font-weight: 700;
  font-size: 19px;
  line-height: 1;
  color: #ffffff;
  padding-top: 22px;
  display: grid;
  grid-template-columns: 1fr auto;
  grid-template-rows: auto 1fr;
  column-gap: 10px;
  overflow-y: auto;
}
@media (max-width: 1279px) {
  .room__price-container {
    grid-template-columns: 1fr;
    grid-template-rows: auto auto 1fr;
    padding-top: 19px;
  }
}
@media (max-width: 767px) {
  .room__price-container {
    font-size: 17px;
  }
}

.room__price-text {
  font-weight: 700;
  font-size: 16px;
  line-height: 1;
  color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif;
  grid-area: 1/1/2/2;
  margin-bottom: 8px;
}
@media (max-width: 1279px) {
  .room__price-text {
    grid-area: 1/1/2/-1;
  }
}
@media (max-width: 767px) {
  .room__price-text {
    font-size: 14px;
    line-height: 1.5;
    margin-bottom: 4px;
  }
}

.room__price-wrapper {
  display: flex;
  flex-wrap: wrap;
  column-gap: 7px;
  align-items: baseline;
}
@media (max-width: 1279px) {
  .room__price-wrapper {
    grid-area: 2/1/3/-1;
  }
}
@media (max-width: 767px) {
  .room__price-wrapper {
    column-gap: 5px;
  }
}

.room__price {
  font-weight: 700;
  font-size: 24px;
  line-height: 1;
  color: #ff4b77;
}
@media (max-width: 767px) {
  .room__price {
    font-size: 20px;
  }
}

.room__sign {
  font-weight: 400;
  font-size: 23px;
  line-height: 1;
  color: #999999;
}
@media (max-width: 767px) {
  .room__sign {
    font-size: 20px;
  }
}

.room__button {
  justify-content: center;
  grid-area: 1/2/3/-1;
  align-self: end;
  width: 180px;
  margin-bottom: 2px;
  padding-left: 5px;
  padding-right: 5px;
  word-break: break-all;
}
@media (max-width: 1279px) {
  .room__button {
    grid-area: 3/1/4/-1;
    width: 100%;
    margin-bottom: 0;
  }
}
@media (max-width: 767px) {
  .room__button {
    font-size: 14px;
    padding-top: 8px;
  }
}
.room__button.is-pressed {
  opacity: 0.6;
}

.room__reservation-info {
  font-weight: 400;
  font-size: 14px;
  line-height: 1.5;
  color: #ffffff;
  font-family: "Open Sans", "Arial", sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  text-align: center;
  background-color: #3c3e43;
  border-radius: 0 0 12px 12px;
  position: absolute;
  bottom: 0;
  left: 0;
  right: 0;
  height: 97px;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
@media (max-width: 1279px) {
  .room__reservation-info {
    height: 161px;
  }
}
@media (max-width: 767px) {
  .room__reservation-info {
    height: 150px;
  }
}

.is-reserved .room__reservation-info {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}

.room__reservation-link {
  color: #ff4b77;
}
@media (hover: hover) {
  .room__reservation-link:hover {
    text-decoration: underline;
  }
}
.room__reservation-link:active {
  text-decoration: none;
  color: #ff97b0;
}

.room__motivation {
  position: absolute;
  top: 0;
  left: 0;
}

.room__image-wrapper {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  background-color: #2d507c;
}
.room__image-wrapper::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-image: linear-gradient(180deg, rgba(10, 34, 64, 0.1) 0%, #0a2240 100%);
}
.room__image-wrapper img {
  object-fit: cover;
  height: 100%;
  width: 100%;
}

.motivation {
  display: flex;
  opacity: 0;
  visibility: hidden;
  pointer-events: none;
  transition: opacity 0.3s ease, visibility 0.3s ease;
}
.motivation--visible {
  opacity: 1;
  visibility: visible;
  pointer-events: all;
}
.motivation img {
  position: absolute;
  top: 0;
  bottom: 0;
  left: 100%;
}

.motivation__text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.5;
  color: #ffffff;
  background-color: #ff7647;
  display: flex;
  align-items: center;
  padding: 4px 4px 5px 17px;
  border-radius: 3px 0 0 3px;
}